import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Header } from "./header.js"
import { Footer } from "./footer.js"
import AppBreadcrumbs from "./AppBreadcrumbs"
import CtaBar from "./CtaBar"
import Bio from "./Bio"
import PostNav from "./PostNav"
import ArchiveNav from "./ArchiveNav"
import RelatedPosts from "./RelatedPosts"
import SideBar from "./sidebar"
import { Box, chakra, Stack } from "@chakra-ui/react"

const SiteContainer = chakra(Box)
const ContentContainer = chakra(Stack)
const MainArticle = chakra("article")

const Layout = ({
  isHomePage,
  breadcrumbs,
  cta,
  author,
  tagPosts,
  categories,
  postId,
  previous,
  next,
  children,
  hasPreviousPage,
  hasNextPage,
  currentPage,
  pageCount,
  archivePath,
  archiveType,
}) => {
  const {
    wp: {
      generalSettings: { title },
    },
  } = useStaticQuery(graphql`
    query LayoutQuery {
      wp {
        generalSettings {
          title
          description
        }
      }
    }
  `)
  return (
    <>
      <SiteContainer
        width={{ base: "100%", lg: "90%" }}
        maxW="1280"
        mx="auto"
        justifyContent="center"
        p="0"
        position="relative"
        boxShadow={{ sm: null, md: "base" }}
        data-is-root-path={isHomePage}
      >
        <Header></Header>

        {breadcrumbs && <AppBreadcrumbs breadcrumbs={breadcrumbs} />}
        <Stack
          as="main"
          direction="row"
          spacing={{base: "1rem", md: "3rem"}}
          mx="auto"
          flexWrap="nowrap"
          bgColor="gray.200"
          width={{ base: "95%", sm: "90%" }}
        >
          <Box width={{ base: "100%", lg: 3 / 4 }}>
            <ContentContainer direction="row">
              <MainArticle
                overflow="hidden"
                background="white"
                borderRadius="md"
                borderColor="blue.400"
                borderTop="8px solid #135d9b"
                boxShadow="md"
              >
                {children}
              </MainArticle>
            </ContentContainer>
            {(previous || next) && <PostNav previous={previous} next={next} />}
            {(hasPreviousPage || hasNextPage) && (
              <ArchiveNav
                hasPreviousPage={hasPreviousPage}
                hasNextPage={hasNextPage}
                currentPage={currentPage}
                pageCount={pageCount}
                archivePath={archivePath}
                archiveType={archiveType}
              />
            )}
            {author && <Bio />}
            {author && (
              <RelatedPosts
                tagPosts={tagPosts}
                categories={categories}
                postId={postId}
              />
            )}
          </Box>
          <SideBar />
        </Stack>
        <Footer></Footer>
      </SiteContainer>
      {cta && <CtaBar cta={cta} />}
    </>
  )
}

export default Layout
