import React from "react"
import { Link as GatsbyLink } from "gatsby"
import {
  chakra,
  Box,
  Stack,
  StackDivider,
  Heading,
  Link,
  AspectRatio,
} from "@chakra-ui/react"
import { ChakraImage } from "./atoms/chakraImage"

const RPContainer = chakra(Box)
const RPBox = chakra(Stack)

const RelatedPosts = ({ tagPosts, postId }) => {
  const rpContent = tagPosts[0]?.posts?.nodes
    .filter(post => post.id !== postId)
    .slice(0, 4)
  return (
    <RPContainer flexWrap="nowrap" direction="row" mx="auto" my={4}>
      <RPBox
        divider={<StackDivider borderColor="gray.200" />}
        overflow="hidden"
        background="white"
        borderRadius="md"
        boxShadow="md"
        className="bio"
        justifyContent="center"
        pb={4}
      >
        <Heading
          as="h3"
          size="md"
          mb="0"
          px={8}
          pt={3}
          pb={2}
          align="left"
          fontWeight="500"
        >
          Related Posts
        </Heading>
        <Stack
          direction="row"
          px={8}
          pb={3}
          pt={4}
          spacing="20px"
          rpContent={rpContent}
        >
          {rpContent &&
            rpContent.map((post, index) => {
              return (
                <Stack key={`rp_${index}`}>
                  <Link as={GatsbyLink} to={post.uri} itemProp="url">
                    <ChakraImage
                      image={
                        post.featuredImage.node.localFile.childImageSharp
                          .gatsbyImageData
                      }
                      alt={post.featuredImage.node.altText}
                      width="100%"
                      objectFit="cover"
                      height="7rem"
                    />
                  </Link>
                  <Link as={GatsbyLink} to={post.uri} itemProp="url">
                    <Heading as="h3" size="xs" fontWeight="semibold">
                      {post.title}
                    </Heading>
                  </Link>
                </Stack>
              )
            })}
        </Stack>
      </RPBox>
    </RPContainer>
  )
}

export default RelatedPosts
