import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useState } from "react"
import { useRelative } from "./particles/hooks/useRelative"
import { useSiteMenuData } from "./particles/hooks/useSiteMenuData"
import Logo from "../../content/assets/idsc-logo.png"
import { Box, Flex, Image, chakra } from "@chakra-ui/react"
import Burger from "./Nav/Burger"
import Search from "./search"
const searchIndices = [{ name: `Posts`, title: `Posts` }]

const HeaderContainer = ({ children }) => {
  return (
    <Box id="headerContainer" bg="white" mb="6">
      {children}
    </Box>
  )
}

const TopHeader = ({ children }) => {
  return (
    <Box
      bg="brand.800"
    >
      {children}
    </Box>
  )
}

const MainHeader = ({ children }) => {
  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      flexDirection="row"
      flexWrap="wrap"
      bg="white"
      w="90%"
      my="0"
      mx="auto"
      overflow="hidden"
      sx={activeLink}
    >
      {children}
    </Box>
  )
}
const activeLink = {
  "[aria-current]": {
    color: "brand.600"
  }
}

const NavMenu = ({ children, isMenuOpen }) => {
  return (
    <Flex
      as="nav"
      display="flex"
      position={{ base: "fixed", md: "static" }}
      top={{ base: "0", md: "unset" }}
      right={{ base: "0", md: "unset" }}
      height={{ base: "100vh", md: "unset" }}
      width={{ base: "300px", md: "unset" }}
      flexDirection={{ base: "column", md: "row" }}
      flexWrap="wrap"
      alignItems="center"
      pt={{
        base: `6rem`,
        md: "unset",
      }}
      transition="transform 0.3s ease-in-out"
      background={{ base: "white", md: "unset" }}
      zIndex={{ base: "1", md: "auto" }}
      transform={{ base: isMenuOpen, md: "unset" }}
    >
      {children}
    </Flex>
  )
}

const NavLink = chakra(Link, {
  baseStyle: {
    marginLeft: { base: "unset", md: "6" },
    textTransform: "uppercase",
    textDecoration: "none",
    fontWeight: "600",
    fontSize: "md",
    color: "rgba(40,40,40, 0.6)",
    letterSpacing: "1px",
    transition: "0.5s ease",
    position: "relative",
    "&:before": {
      content: "''",
      position: "absolute",
      top: "10px",
      left: "0",
      height: "2px",
      backgroundColor: "brand.600",
      width: "100%",
      transform: "translateY(40px)",
      opacity: "0",
      transition: "0.5s ease",
    },
    _hover: {
      color: "brand.600",
      "&:before": {
        opacity: { base: "0", md: "1" },
        transform: "translateY(24px)",
      },
    },
  },
})

export const Header = ({ siteTitle }) => {
  Header.propTypes = {
    siteTitle: PropTypes.string,
  }

  Header.defaultProps = {
    siteTitle: ``,
  }
  const [open, setOpen] = useState(false)

  const toggleMenu = () => {
    setOpen(!open)
  }

  const menuData = useSiteMenuData()
  const headerData = menuData.nodes.find(menu => menu.slug === "main")
  const isMenuOpen = open ? "translateX(0)" : "translateX(100%)"

  return (
    <HeaderContainer>
      <TopHeader>
        <Box
          display="flex"
          flexDirection="row"
          justifyContent="flex-end"
          py="1"
          w="90%"
          my="0"
          mx="auto"
        >
        <Search indices={searchIndices} />

        </Box>
      </TopHeader>
      <MainHeader>
        <Link to="/">
          <Image maxW="210px" my="4" src={Logo} />
        </Link>
        <Burger
          open={open}
          menuItems={headerData.menuItems.nodes}
          onChildClick={toggleMenu}
        />
        <NavMenu isMenuOpen={isMenuOpen}>
          {headerData.menuItems.nodes.map(item => (
            <MenuItem {...item} key={item.id.toString()} />
          ))}
        </NavMenu>
      </MainHeader>
    </HeaderContainer>
  )
}

const MenuItem = ({ url, label }) => {
  const relativeLink = useRelative(url)
  return <NavLink to={relativeLink}>{label}</NavLink>
}
