import { useStaticQuery, graphql } from "gatsby"
export const useSiteMenuData = () => {
  const { allWpMenu } = useStaticQuery(
    graphql`
      query MenuData {
        allWpMenu {
          nodes {
            id
            name
            slug
            menuItems {
              nodes {
                id
                label
                title
                url
              }
            }
          }
        }
      }
    `
  )
  return allWpMenu
}
