import React from "react"
import parse from "html-react-parser"
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
} from "@chakra-ui/react"

const AppBreadcrumbs = ({ breadcrumbs, ...other }) => {
  return (
    <Breadcrumb
      width={{ base: "95%", sm: "90%" }}
      mx="auto"
      mb="6"
      fontSize={{ base: "xs", md: "md" }}
      spacing={{ base: "1"}}
      separator="›"
    >
      {breadcrumbs &&
        breadcrumbs.map((crumb, index, arr) => {
          return (
            <BreadcrumbItem key={index}>
              <BreadcrumbLink href={crumb.url} key={index} color={index !== arr.length - 1 ? 'brand.400' : "gray.900"} >
                {parse(crumb.text)}
              </BreadcrumbLink>
            </BreadcrumbItem>
          )
        })}
    </Breadcrumb>
  )
}

export default AppBreadcrumbs
