/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import {
  chakra,
  Stack,
  StackDivider,
  Box,
  Heading,
  Image,
  Text,
  Link,
  Icon,
} from "@chakra-ui/react"
import { FaLinkedinIn } from "react-icons/fa"

const BioContainer = chakra(Box)
const BioBox = chakra(Stack)
const BioImage = chakra("img")

const Bio = () => {
  const { author } = useStaticQuery(graphql`
    query BioQuery {
      # if there was more than one user, this would need to be filtered
      author: wpUser(name: { eq: "Tom Smith" }) {
        name
        seo {
          social {
            linkedIn
          }
        }
        description
        avatar {
          url
        }
      }
    }
  `)

  const avatarUrl = author?.avatar?.url

  return (
    <BioContainer flexWrap="nowrap" direction="row" mx="auto" my={4}>
      <BioBox
        divider={<StackDivider borderColor="gray.200" />}
        overflow="hidden"
        background="white"
        borderRadius="md"
        boxShadow="md"
        className="bio"
        justifyContent="center"
        pb={4}
      >
        <Heading
          as="h3"
          size="md"
          mb="0"
          px={8}
          pt={3}
          pb={2}
          align="left"
          fontWeight="500"
        >
          About The Author
        </Heading>
        <Stack direction="row" px={8} pb={3} pt={4} spacing="24px">
          {avatarUrl && (
            <Box w={2 / 12}>
              <Image
                alt={author?.name || ``}
                className="bio-avatar"
                w="100%"
                src={avatarUrl}
              />
            </Box>
          )}
          {author?.name && (
            <Box w={10 / 12}>
              <Heading as="h4" size="md" fontWeight="500" pb={2}>
                {author.name}
              </Heading>
              <Text size="sm">{author?.description || null}</Text>
              <Link
                href={author.seo.social.linkedIn}
                isExternal
                color="gray.400"
                _hover={{ color: "gray.600" }}
              >
                <Icon as={FaLinkedinIn} />
              </Link>
            </Box>
          )}
        </Stack>
      </BioBox>
    </BioContainer>
  )
}

export default Bio
