import React from "react"
import { Formik, Form, Field, ErrorMessage } from "formik"
import { Box, VStack, Button, Text, Heading, chakra } from "@chakra-ui/react"
import TextField from "./TextField"
import * as Yup from "yup"
import addToMailchimp from "gatsby-plugin-mailchimp"
import { StaticQuery, graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"

const FormImage = chakra(GatsbyImage, {
  baseStyle: {
    width: "80%",
    overflow: "hidden",
  },
})

const AppForm = () => {
  const validate = Yup.object({
    firstName: Yup.string()
      .max(15, "Must be 15 characters or less")
      .required("Your first name is required"),
    lastName: Yup.string()
      .max(20, "Must be 20 characters or less")
      .required("Your last name is required"),
    email: Yup.string()
      .email("Please provide a valid email address")
      .required("A valid email address is required"),
  })
  const verifyCallback = response => {
    console.log(response)
  }
  return (
    <StaticQuery
      query={graphql`
        query formQuery {
          file(relativePath: { eq: "subscribe-banner.png" }) {
            childImageSharp {
              gatsbyImageData(
                width: 400
                placeholder: TRACED_SVG
                layout: CONSTRAINED
              )
            }
          }
        }
      `}
      render={data => (
        <Box>
          <Box px={6} py={8} bgColor="gray.700" textAlign="center">
            <Heading color="white" fontSize="1.5rem" fontWeight="600">
              4 out of 5 Drivers Agree
            </Heading>
            <Text color="gray.300" fontSize=".9rem" py="1rem">
              Our Newsletter:
              <br />
              1. Is sent to yout inbox
              <br />
              2. Includes words and pictures
              <br />
            </Text>
            <FormImage
              mx="auto"
              image={data?.file?.childImageSharp?.gatsbyImageData}
              alt="Subscribe"
            />
          </Box>
          <Box px={6} py={8} bgColor="#c9c6b9">
            <Formik
              initialValues={{
                firstName: "",
                lastName: "",
                email: "",
              }}
              validationSchema={validate}
              onSubmit={async values => {
                const result = await addToMailchimp(values.email, values)
                console.log(result)
              }}
            >
              {/* <Recaptcha sitekey="6Lf-Yc8UAAAAAICazIJTf_vcvfkc2lW4lczMeyxm"
             render="explicit"
             verifyCallback={verifyCallback}
             /> */}
              {formik => (
                <VStack as={Form} spacing="0.8rem">
                  <TextField
                    size="lg"
                    type="text"
                    label="First Name"
                    name="firstName"
                    placeholder="First Name"
                  />
                  <TextField
                    size="lg"
                    type="text"
                    label="Last Name"
                    name="lastName"
                    placeholder="Last Name"
                  />
                  <TextField
                    size="lg"
                    type="email"
                    label="Email"
                    name="email"
                    placeholder="Email"
                  />
                  <Button
                    size="lg"
                    w="100%"
                    colorScheme="green"
                    textTransform="uppercase"
                    type="submit"
                  >
                    Subscribe!
                  </Button>
                </VStack>
              )}
            </Formik>
            <Text
              mt="1.5rem"
              color="gray.600"
              textAlign="center"
              fontSize="0.8rem"
              fontStyle="italic"
            >
              Results may vary.
              <br />
              We're serious but have fun.
              <br />
              Never text and drive.
            </Text>
          </Box>
        </Box>
      )}
    />
  )
}

export default AppForm
