import React from "react"
import { Link as GatsbyLink } from "gatsby"
import parse from "html-react-parser"
import { Link, Box, Stack, Button, chakra } from "@chakra-ui/react"
import { FaChevronRight, FaChevronLeft } from "react-icons/fa"

const ArchiveNavContainer = chakra(Box)

const ArchiveNav = ({
  hasPreviousPage,
  hasNextPage,
  currentPage,
  archivePath,
  archiveType,
  pageCount,
}) => {
  let previousLink = null
  let nextLink = null
  if (2 === currentPage) {
    previousLink = archivePath
  } else if (2 < currentPage) {
    if (archiveType === "tribeEvent") {
      previousLink = `${archivePath}list/page/${currentPage - 1}`
    } else {
      previousLink = `${archivePath}page/${currentPage - 1}`
    }
  }

  if (hasNextPage && archiveType === "tribeEvent") {
    nextLink = `${archivePath}list/page/${currentPage + 1}`
  } else if (hasNextPage && archiveType !== "tribeEvent") {
    nextLink = `${archivePath}page/${currentPage + 1}`
  }
  return (
    <ArchiveNavContainer
      as="nav"
      flexWrap="nowrap"
      direction="row"
      mx="auto"
      my={4}
    >
      <Stack
        as="ul"
        direction="row"
        listStyleType="none"
        justify="center"
        fontSize="sm"
        color="brand.400"
      >
        <Stack>
          {hasPreviousPage && (
            <>
              <Link
                as={GatsbyLink}
                to={previousLink}
                rel="prev"
                _hover={{ color: "gray.700", textDecoration: "none" }}
              >
                <Button
                  colorScheme="brand"
                  leftIcon={<FaChevronLeft />}
                  sx={{ span: { margin: "0" } }}
                />
              </Link>
            </>
          )}
        </Stack>

        <Stack>
          {hasNextPage && (
            <>
              <Link
                as={GatsbyLink}
                to={nextLink}
                rel="next"
                _hover={{ color: "gray.700", textDecoration: "none" }}
              >
                <Button
                  colorScheme="brand"
                  alignSelf="flex-end"
                  rightIcon={<FaChevronRight />}
                  sx={{ span: { margin: "0" } }}
                />
              </Link>
            </>
          )}
        </Stack>
      </Stack>
    </ArchiveNavContainer>
  )
}

export default ArchiveNav
